import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppearanceComponent } from './components/appearance.component';
import { SecurityComponent } from './components/security.component';
import { InformationSourcesComponent } from './components/information-sources.component';
import { AccountComponent } from './components/account.component';
import { ApiComponent } from './components/api.component';
import { ApiGuideComponent } from './components/api-guide.component';

const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: '',
                children: [
                    { path: '', component: AppearanceComponent },
                    { path: 'security', component: SecurityComponent },
                    { path: 'informationsources', component: InformationSourcesComponent },
                    { path: 'account', component: AccountComponent },
                    { path: 'api', component: ApiComponent },
                    { path: 'api-guide', component: ApiGuideComponent }
                ]
            }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AdminRoutingModule {}
