import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { L10N_LOCALE, L10nLocale } from 'angular-l10n';
import { InformationSourceViewModel, SharePointListInfo } from '../admin.models';
import { Store, select } from '@ngrx/store';
import { CompositeModel } from '../admin.models';
import { Subject, Observable } from 'rxjs';
import * as fromRoot from '../../reducers';
import * as fromAdmin from '../admin.reducers';
import * as AdminActions from '../admin.actions';
import { takeUntil, debounceTime, take, map, filter } from 'rxjs/operators';
import { ToastError } from 'src/app/shared/shared.actions';

@Component({
    selector: 'toc-admin-information-sources',
    templateUrl: 'information-sources.component.html'
})
export class InformationSourcesComponent implements OnInit, OnDestroy {
    sources: InformationSourceViewModel[] = [];
    selectedSource: InformationSourceViewModel = null;
    selectedListId: string = null;
    selectedSharePointFieldId: string = null;
    sharePointLists$: Observable<SharePointListInfo[]>;
    sharePointListFields$: Observable<{ [paramName: string]: string }[]>;
    private unsubscribe: Subject<void> = new Subject();

    constructor(@Inject(L10N_LOCALE) public locale: L10nLocale, private store: Store<fromRoot.State>) {
        this.store.pipe(select(fromAdmin.selectCompositeModel), takeUntil(this.unsubscribe)).subscribe(model => {
            if (model === null) {
                this.store.dispatch(AdminActions.loadCompositeModel());
                return;
            }
            this.sources = model.InformationSources.map(x => ({ ...x }));
        });
        this.store.pipe(select(fromAdmin.selectSelectedSourceInfo), takeUntil(this.unsubscribe), filter(x => x !== null)).subscribe(info => {
            if (this.selectedSource.InformationSourceId === info.InformationSourceId) {
                this.selectedSource = { ...info };
            }
        });
        this.store.pipe(select(fromAdmin.selectDeletedSourceId), takeUntil(this.unsubscribe), filter(x => x !== null)).subscribe(id => {
            this.sources = this.sources.filter(x => (x.InformationSourceId !== id));
            this.store.dispatch(AdminActions.loadCompositeModel());
        });
        this.store.pipe(select(fromAdmin.selecteSavedSourceId), takeUntil(this.unsubscribe), filter(x => x !== null)).subscribe(id => {
            if (this.selectedSource) {
                const idx = this.sources.findIndex(x => x.InformationSourceId === id);
                if (idx === -1) {
                    this.sources.push({ ...this.selectedSource, InformationSourceId: id });
                    this.selectedSource = { ...this.sources.find(x => x.InformationSourceId === id) };
                } else {
                    this.sources[idx] = { ...this.selectedSource };
                    this.selectedSource = { ...this.sources[idx] };
                }
            }
            this.store.dispatch(AdminActions.clearSavedSourceId());
        });
        this.sharePointLists$ = this.store.pipe(select(fromAdmin.selectSharePointLists));
        this.sharePointListFields$ = this.store.pipe(select(fromAdmin.selectSharePointListFields));
    }

    ngOnInit() {}

    testInformationSource(id: string) {
        this.store.dispatch(AdminActions.testInformationSource({ id }));
    }

    newSource() {
        this.selectedSource = {
            CanEdit: false,
            ConnectionString: '',
            Description: '',
            FriendlyTypeName: '',
            InformationSourceId: '',
            Query: '',
            SharePointList: '',
            SharePointListColumn: '',
            SourceName: '',
            SourceType: 'SQLServer'
        };
    }

    editSource(sourceId: string) {
        const source = this.sources.find(x => x.InformationSourceId === sourceId);
        if (source) {
            this.selectedSource = { ...source };
        }
        this.store.dispatch(AdminActions.loadInformationSource({ sourceId }));
    }

    removeSource(id: string) {
        this.store.dispatch(AdminActions.deleteInformationSource({ id }));
    }

    saveSource() {
        if (this.selectedSource === null) {
            this.store.dispatch(new ToastError('Please select an existing or create a new source first'));
            return;
        }
        this.store.dispatch(AdminActions.saveInformationSource({ source: this.selectedSource }));
    }

    updateFieldList() {
        if (this.selectedListId) {
            this.store.dispatch(AdminActions.getSharePointListFields({ id: this.selectedListId }));
        }
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
