import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { L10N_LOCALE, L10nLocale } from 'angular-l10n';
import { Store, select } from '@ngrx/store';
import { CompositeModel } from '../admin.models';
import { Subject, Observable } from 'rxjs';
import * as fromRoot from '../../reducers';
import * as fromAdmin from '../admin.reducers';
import * as AdminActions from '../admin.actions';
import { takeUntil, debounceTime, take, map } from 'rxjs/operators';
import { FormGroupState, SetValueAction } from 'ngrx-forms';
import { FileUploader } from 'ng2-file-upload';
import { ToastSuccess } from 'src/app/shared/shared.actions';
import { AdminService } from '../services/admin.service';
import { Toast } from 'ngx-toastr';

@Component({
    selector: 'toc-admin-appearance',
    styleUrls: ['./appearance.component.scss'],
    templateUrl: './appearance.component.html'
})
export class AppearanceComponent implements OnInit, OnDestroy {
    model$: Observable<CompositeModel>;
    brandColor$: Subject<string> = new Subject();
    appearanceFormState$: Observable<FormGroupState<fromAdmin.AppearanceFormValue>>;
    colorErrorMessage = '';
    href = '';
    csshref = '';
    showRemoveLogo = false;
    public logoUploader: FileUploader = new FileUploader({
        isHTML5: true,
        allowedMimeType: ['image/*', 'image/jpeg', 'image/png', 'image/bmp'],
        url: '/api/admin/logoblobux'
    });
    public cssUploader: FileUploader = new FileUploader({
        isHTML5: true,
        allowedMimeType: ['text/*', 'text/css', 'text/plain', 'text/html'],
        url: '/api/admin/customcss'
    });
    private unsubscribe: Subject<void> = new Subject();

    constructor(@Inject(L10N_LOCALE) public locale: L10nLocale, private store: Store<fromRoot.State>, private adminService: AdminService) {
        this.model$ = store.pipe(select(fromAdmin.selectCompositeModel));
        this.model$.pipe(takeUntil(this.unsubscribe)).subscribe(model => {
            if (model === null) {
                this.store.dispatch(AdminActions.loadCompositeModel());
                return;
            }
            this.store.dispatch(AdminActions.setAppearanceFormFromCompositeModel({ model }));
            this.showRemoveLogo = model.Settings.HasCustomLogo;
            this.csshref = model.CustomCssPath;
            this.href = model.LogoPath;
            if (this.href) {
                this.href = this.href + '?decache=' + Math.random();
            }
        });
        this.appearanceFormState$ = store.pipe(select(fromAdmin.selectAppearanceFormState));
        this.brandColor$.pipe(takeUntil(this.unsubscribe), debounceTime(50)).subscribe(color => {
            this.store.dispatch(new SetValueAction(fromAdmin.initialAppearanceFormState.controls.brandColor.id, color));
        });
    }

    ngOnInit() {
        this.logoUploader.onCompleteItem = (item, response, status, headers) => {
            console.log(item, response, status, headers);
            const resp = JSON.parse(response);
            if (resp['status'] === 'OK') {
                this.href = resp['href'] + '?decache=' + Math.random();
                this.showRemoveLogo = true;
                this.store.dispatch(new ToastSuccess(''));
            }
        };
        this.cssUploader.onCompleteItem = (item, response, status, headers) => {
            console.log(item, response, status, headers);
            const resp = JSON.parse(response);
            if (resp['status'] === 'OK') {
                this.csshref = resp['href'] + '?decache=' + Math.random();
                this.store.dispatch(new ToastSuccess(''));
            }
        };
    }

    updateBrandColor($event: string) {
        this.brandColor$.next($event);
    }

    saveAppearance() {
        this.appearanceFormState$
            .pipe(
                take(1),
                map(appearanceForm => {
                    const isOk = /(^#[0-9A-F]{6}$)|(^#[0-9A-F]{3}$)/i.test(appearanceForm.controls.brandColor.value);
                    if (!isOk) {
                        this.colorErrorMessage = 'Please enter a valid brand color value';
                        return;
                    }
                    this.colorErrorMessage = '';
                    this.store.dispatch(AdminActions.saveAppearance({ form: appearanceForm.value }));
                    this.store.dispatch(new ToastSuccess(""));
                })
            )
            .subscribe();
    }

    uploadLogo() {
        this.href = '';
        this.logoUploader.uploadAll();
    }

    uploadCss() {
        this.csshref = '';
        this.cssUploader.uploadAll();
    }

    removeLogo() {
        this.adminService.removeLogo().pipe(takeUntil(this.unsubscribe)).subscribe(() => {
            this.href = '';
            this.showRemoveLogo = false;
            this.store.dispatch(new ToastSuccess(''));
            this.saveAppearance();
        });
    }

    saveLogo() {
        this.saveAppearance();
    }

    removeCss() {
        this.adminService.removeCss().pipe(takeUntil(this.unsubscribe)).subscribe(() => {
            this.store.dispatch(new ToastSuccess(''));
            this.csshref = '';
        });
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
