import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { L10N_LOCALE, L10nLocale } from 'angular-l10n';
import * as Resources from '../../strings';
import * as fromRoot from '../../reducers';
import * as fromAdmin from '../admin.reducers';
import * as AdminActions from '../admin.actions';
import { Observable, Subject } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { takeUntil } from 'rxjs/operators';
import { CompositeModel } from '../admin.models';
import { ConfirmationModalOptions, SharedModals } from 'src/app/shared/shared.models';
import { DialogShow } from 'src/app/shared/shared.actions';

@Component({
    selector: 'toc-admin-account',
    templateUrl: 'account.component.html'
})
export class AccountComponent implements OnInit, OnDestroy {
    Resources = Resources;
    adminEmail = '';
    companyName = '';
    disableCacheSetting$: Observable<boolean>;
    model$: Observable<CompositeModel>;
    private unsubscribe: Subject<void> = new Subject();

    constructor(@Inject(L10N_LOCALE) public locale: L10nLocale, private store: Store<fromRoot.State>) {
        this.disableCacheSetting$ = this.store.pipe(select(fromAdmin.selectDisableCacheSetting));
        this.disableCacheSetting$.pipe(takeUntil(this.unsubscribe)).subscribe(x => {
            if (x === null) {
                this.store.dispatch(AdminActions.loadCacheSetting());
            }
        });
        this.model$ = store.pipe(select(fromAdmin.selectCompositeModel));
        this.model$.pipe(takeUntil(this.unsubscribe)).subscribe(model => {
            if (model === null) {
                this.store.dispatch(AdminActions.loadCompositeModel());
                return;
            }
            this.adminEmail = model.AdminEmailAndCompanyName.AdminEmail;
            this.companyName = model.AdminEmailAndCompanyName.CompanyName;
        });
    }

    ngOnInit() {}

    saveCacheSetting($event: boolean) {
        this.store.dispatch(AdminActions.saveCacheSetting({ value: $event }));
    }

    saveAdminEmail() {
        this.store.dispatch(AdminActions.saveAdminEmail({ AdminEmail: this.adminEmail, CompanyName: this.companyName }));
    }

    deleteAccount() {
        const confirmationOptions: ConfirmationModalOptions = {
            cancelButtonText: Resources.Strings.BUTTON.Cancel,
            text: Resources.Strings.TXT.DeleteAccountConfirmationText,
            title: Resources.Strings.TXT.DeleteAccountConfirmationDialogTitle,
            successButtonText: Resources.Strings.BTN.DeleteAccountConfirmation,
            //successAction: new SaveColumnSettings(this.columnSettings)
            callback: () => {
                (document.getElementById('delete-account-form') as HTMLFormElement).submit();
            }
        };
        this.store.dispatch(
            new DialogShow({
                dialogName: SharedModals.ConfirmationDialog,
                options: {
                    options: confirmationOptions
                }
            })
        );
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
