import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { L10N_LOCALE, L10nLocale } from 'angular-l10n';
import { Store, select } from '@ngrx/store';
import * as fromRoot from '../../reducers';
import * as fromAdmin from '../admin.reducers';
import * as AdminActions from '../admin.actions';
import { Observable, Subject } from 'rxjs';
import { CompositeModel, CreatorInfo, SecurityGroup, AvailableEntries, Entry, SecurityGroupEntry, SecurityChartViewModel } from '../admin.models';
import * as Resources from '../../strings';
import { takeUntil, map, mergeMap, take, withLatestFrom } from 'rxjs/operators';
import { ChartService } from 'src/app/core/services/chart.service';
import { ChartNode } from 'src/app/models';
import { TypeaheadMatch } from 'ngx-bootstrap/typeahead/public_api';
import { AdminService } from '../services/admin.service';
import { Title } from '@angular/platform-browser';

@Component({
    selector: 'toc-admin-security',
    templateUrl: './security.component.html'
})
export class SecurityComponent implements OnInit, OnDestroy {
    Resources = Resources;
    model$: Observable<CompositeModel>;
    availableEntries$: Observable<AvailableEntries>;
    charts$: Observable<SecurityChartViewModel[]>;
    chartRequests$: Observable<{ [paramName: string]: any }>;
    chartExternalRequests$: Observable<{ [paramName: string]: any }>;
    peoplePickerSource$: Observable<boolean>;
    peoplePickerSearchString: string;
    securityUserPickerSource$: Observable<boolean>;
    securityUserPickerString: string;
    newCreator: ChartNode = null;
    addedUser: ChartNode = null;
    addedGroup: Entry = null;
    creators: CreatorInfo[];
    newSecurityGroupName = '';
    selectedSecurityGroup: SecurityGroup = null;
    globalExternalUsersSetting = false;
    private unsubscribe: Subject<void> = new Subject();

    constructor(@Inject(L10N_LOCALE) public locale: L10nLocale, private store: Store<fromRoot.State>, private chartService: ChartService, private adminService: AdminService, private title: Title) {
        this.model$ = store.pipe(select(fromAdmin.selectCompositeModel));
        this.model$.pipe(takeUntil(this.unsubscribe)).subscribe(model => {
            if (model === null) {
                this.store.dispatch(AdminActions.loadCompositeModel());
                return;
            }
            this.creators = model.Creators ? model.Creators.map(x => ({ id: x, name: x})) : [];
            this.updateCreatorInfo();
            if (this.selectedSecurityGroup !== null) {
                this.selectedSecurityGroup = model.SecurityGroups.find(x => x.Id === this.selectedSecurityGroup.Id);
            }
            this.globalExternalUsersSetting = model.GlobalExternalUsersSetting;
        });
        this.availableEntries$ = store.pipe(select(fromAdmin.selectAvailableEntries));
        this.availableEntries$.pipe(takeUntil(this.unsubscribe)).subscribe(entries => {
            if (entries === null) {
                this.store.dispatch(AdminActions.loadCompanyGroups());
                return;
            }
        });
        this.charts$ = store.pipe(select(fromAdmin.selectCharts));
        this.charts$.pipe(takeUntil(this.unsubscribe)).subscribe(charts => {
            if (charts === null) {
                this.store.dispatch(AdminActions.loadCharts());
                return;
            }
        });
        title.setTitle('Settings - Security');
        this.chartRequests$ = this.store.pipe(select(fromAdmin.selectChartRequests));
        this.chartExternalRequests$ = this.store.pipe(select(fromAdmin.selectChartExternalRequests));
    }

    ngOnInit() {
        this.store.pipe(select(fromAdmin.selectSelectedSecurityGroupId), withLatestFrom(this.model$), takeUntil(this.unsubscribe)).subscribe(([id, model]) => {
            if (id !== 0 && model && model.SecurityGroups && model.SecurityGroups.length > 0) {
                const group = model.SecurityGroups.find(x => x.Id === id);
                if (group) {
                    this.selectedSecurityGroup = group;
                    this.newSecurityGroupName = '';
                }
            }
        });
        this.peoplePickerSource$ = new Observable<string>(observer => {
            observer.next(this.peoplePickerSearchString);
        }).pipe(
            mergeMap(x => {
                return this.chartService.searchOrganization(x).pipe(
                    //tap(y => console.log(y)),
                    map(data => data.map(item => {
                        if (item.JobTitle) {
                            item.TypeaheadTitle = `${item.DisplayName} - ${item.JobTitle}`;
                        } else {
                            item.TypeaheadTitle = item.DisplayName;
                        }
                        return item;
                    }))
                );
            })
        );
        this.securityUserPickerSource$ = new Observable<string>(observer => {
            observer.next(this.securityUserPickerString);
        }).pipe(
            mergeMap(x => {
                return this.chartService.searchOrganization(x).pipe(
                    //tap(y => console.log(y)),
                    map(data => data.map(item => {
                        if (item.JobTitle) {
                            item.TypeaheadTitle = `${item.DisplayName} - ${item.JobTitle}`;
                        } else {
                            item.TypeaheadTitle = item.DisplayName;
                        }
                        return item;
                    }))
                );
            })
        );
    }

    onSelectPerson($event: TypeaheadMatch) {
        console.log($event);
        this.newCreator = $event.item;
    }

    onSelectSecurityPerson($event: TypeaheadMatch) {
        console.log($event);
        this.addedUser = $event.item;
    }

    addCreator() {
        if (this.newCreator === null) {
            return;
        }
        this.model$.pipe(
            take(1),
            map(model => {
                if (model.Creators.findIndex(x => x === this.newCreator.UniqueId) !== -1) {
                    return;
                }
                this.store.dispatch(AdminActions.saveCreators({ creators: [...model.Creators, this.newCreator.UniqueId] }));
                this.newCreator = null;
                this.peoplePickerSearchString = '';
            })
        ).subscribe();
    }

    removeCreator(id: string) {
        this.model$.pipe(
            take(1),
            map(model => {
                this.store.dispatch(AdminActions.saveCreators({ creators: model.Creators.filter(x => x !== id)}));
                this.newCreator = null;
                this.peoplePickerSearchString = '';
            })
        ).subscribe();
    }

    updateCreatorInfo() {
        this.creators.forEach(creator => {
            this.adminService.getPersonName(creator.id).subscribe(name => {
                creator.name = name;
            });
        });
    }

    addSecurityGroup() {
        if (this.newSecurityGroupName === '' || this.newSecurityGroupName === null) {
            return;
        }
        this.store.dispatch(AdminActions.createSecurityGroup({ name: this.newSecurityGroupName }));
    }

    removeSecurityGroup(groupId : number) {
        this.store.dispatch(AdminActions.removeSecurityGroup({ id: groupId }));
    }

    addToSecurityGroup(type: 'user' | 'group') {
        console.log('adding', type);
        if (!this.selectedSecurityGroup) {
            return;
        }
        const securityGroupEntry: SecurityGroupEntry = {
            Id: 0,
            SecurityGroupId: this.selectedSecurityGroup.Id
        };
        if (type === 'group') {
            securityGroupEntry.GroupName = this.addedGroup.value;
        }
        if (type === 'user') {
            securityGroupEntry.UserId = this.addedUser.UniqueId;
            securityGroupEntry.UserName = this.addedUser.DisplayName;
            this.securityUserPickerString = '';
        }
        this.store.dispatch(AdminActions.addSecurityGroupEntry({ entry: securityGroupEntry }));
        this.addedUser = null;
        this.addedGroup = null;
    }

    removeSecurityGroupEntry(id: number) {
        this.store.dispatch(AdminActions.removeSecurityGroupEntry({ id }));
    }

    hasItems(type: 'user' | 'group'): boolean {
        if (this.selectedSecurityGroup === null || this.selectedSecurityGroup.SecurityGroupEntries == null) {
            return false;
        }
        if (type === 'user') {
            return this.selectedSecurityGroup.SecurityGroupEntries.findIndex(x => !!x.UserId || !!x.UserName) !== -1;
        }
        if (type === 'group') {
            return this.selectedSecurityGroup.SecurityGroupEntries.findIndex(x => !!x.GroupName) !== -1;
        }
    }

    saveGlobalExternalSetting(value: boolean) {
        this.store.dispatch(AdminActions.saveGlobalExternalSetting({ value }));
    }

    saveExternalUsersSetting(chart: SecurityChartViewModel, $event: boolean) {
        this.store.dispatch(AdminActions.saveExternalUsersSetting( { chart: {...chart, areExternalUsersAllowed: $event }}));
    }

    userEntry = (entry: SecurityGroupEntry) => {
        return !!entry.UserId || !!entry.UserName;
    }

    groupEntry = (entry: SecurityGroupEntry) => {
        return !!entry.GroupName;
    }

    saveChart(chart: SecurityChartViewModel, prop: string, $event: number) {
        console.log($event);
        const newChart = {...chart};
        newChart[prop] = $event;
        this.store.dispatch(AdminActions.saveChart({ chart: newChart }));
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
