import { Actions, ofType, createEffect } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import * as fromAdmin from './admin.reducers';
import * as AdminActions from './admin.actions';
import { of } from 'rxjs';
import { switchMap, catchError, map, mergeMap } from 'rxjs/operators';
import { ToastError, ToastSuccess } from '../shared/shared.actions';
import { AdminService } from './services/admin.service';
import { ActionCreator, Creator, resultMemoize, Action } from '@ngrx/store';

@Injectable()
export class AdminEffects {
    constructor(
        private actions$: Actions,
        private adminService: AdminService
    ) { }

    loadCompositeModel$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.loadCompositeModel),
        switchMap(() => this.adminService.loadAdminCompositeData().pipe(map(data => AdminActions.loadCompositeModelSuccess({ model: data })))),
        catchError(() => of(AdminActions.loadCompositeModelFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load page data')])))
    ));

    loadCompanyGroups$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.loadCompanyGroups),
        switchMap(() => this.adminService.loadCompanyGroups().pipe(map(data => AdminActions.loadCompanyGroupsSuccess({ entries: data })))),
        catchError(() => of(AdminActions.loadCompanyGroupsFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load company groups list')])))
    ));

    loadCharts$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.loadCharts),
        switchMap(() => this.adminService.loadSecurityCharts().pipe(map(data => AdminActions.loadChartsSuccess({ charts: data })))),
        catchError(() => of(AdminActions.loadChartsFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load charts list')])))
    ));

    loadCacheSetting$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.loadCacheSetting),
        switchMap(() => this.adminService.loadCacheSetting().pipe(
            map(data => AdminActions.loadCacheSettingSuccess({ value: data })),
            catchError(() => of(AdminActions.loadCacheSettingFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load cache setting')])))
        ))
    ));

    saveAppearance$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveAppearance),
        switchMap(action => this.adminService.saveAppearance(action.form).pipe(map(data => AdminActions.saveAppearanceSuccess())))
    ));

    saveCreators$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveCreators),
        switchMap(action => this.adminService.postCreators(action.creators).pipe(map(data => AdminActions.saveCreatorsSuccess({ creators: data }))))
    ));

    createSecurityGroup$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.createSecurityGroup),
        mergeMap(action => this.adminService.addSecurityGroup(action.name).pipe(map(data => AdminActions.createSecurityGroupSuccess({ group: data })))),
        catchError(() => of(AdminActions.createSecurityGroupFailure()).pipe(mergeMap(x => [x, new ToastError('Could not save security group')])))
    ));

    removeSecurityGroup$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.removeSecurityGroup),
        mergeMap(action => this.adminService.removeSecurityGroup(action.id).pipe(map(data => AdminActions.removeSecurityGroupSuccess({ groups: data })))),
        catchError(() => of(AdminActions.removeSecurityGroupFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to remove security group')])))
    ));

    addSecurityGroupEntry$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.addSecurityGroupEntry),
        mergeMap(action => this.adminService.addSecurityGroupEntry(action.entry).pipe(map(data => AdminActions.addSecurityGroupEntrySuccess({ group: data })))),
        catchError(() => of(AdminActions.addSecurityGroupEntryFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to add security group entry')])))
    ));

    removeSecurityGroupEntry$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.removeSecurityGroupEntry),
        mergeMap(action => this.adminService.removeSecurityGroupEntry(action.id).pipe(map(data => AdminActions.removeSecurityGroupEntrySuccess({ group: data })))),
        catchError(() => of(AdminActions.removeSecurityGroupEntryFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to remove security group entry')])))
    ));

    saveGlobalExternalUsersSetting$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveGlobalExternalSetting),
        switchMap(action => this.adminService.saveGlobalExternalUsersSetting(action.value).pipe(map(data => AdminActions.saveGlobalExternalSettingSuccess({ value: data })))),
        catchError(() => of(AdminActions.saveGlobalExternalSettingFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to save settings')])))
    ));

    saveExternalUsersSetting$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveExternalUsersSetting),
        mergeMap(action => this.adminService.saveExternalUsersSetting(action.chart).pipe(
            map(data => AdminActions.saveExternalUsersSettingSuccess({ chart: data })),
            catchError(() => of(AdminActions.saveExternalUsersSettingFailure({ chart: action.chart })).pipe(mergeMap(x => [x, new ToastError('Failed to save settings')])))
        )
    )));

    saveSecurityChart$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveChart),
        mergeMap(action => this.adminService.saveSecurityChart(action.chart).pipe(
            map(data => AdminActions.saveChartSuccess({ chart: data })),
            catchError(() => of(AdminActions.saveChartFailure({ chart: action.chart })).pipe(mergeMap(x => [x, new ToastError('Failed to save settings')])))
        )
    )));

    saveAdminEmail$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveAdminEmail),
        mergeMap(action => this.adminService.saveAdminEmail(action.AdminEmail, action.CompanyName).pipe(
            map(data => AdminActions.saveAdminEmailSuccess({ AdminEmail: action.AdminEmail, CompanyName: action.CompanyName })),
            catchError(() => of(AdminActions.saveAdminEmailFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to save settings')])))
        ))
    ));

    saveCacheSetting$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveCacheSetting),
        mergeMap(action => this.adminService.saveCacheSetting(action.value).pipe(
            mergeMap(data => [AdminActions.saveCacheSettingSuccess({ value: data }), new ToastSuccess('')]),
            catchError(() => of(AdminActions.saveCacheSettingFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to save settings')])))
        ))
    ));

    loadInformationSource$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.loadInformationSource),
        mergeMap(action =>
            this.adminService.getInfoSource(action.sourceId).pipe(
                map(data => AdminActions.loadInformationSourceSuccess({ source: data })),
                catchError(error => of(AdminActions.loadInformationSourceFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load information source')]))))
            )
        )
    );

    getSharePointLists$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.getSharePointLists),
        switchMap(() =>
            this.adminService.getSharePointLists().pipe(
                map(data => AdminActions.getSharePointListsSuccess({ lists: data })),
                catchError(error => of(AdminActions.getSharePointListsFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load SharePoint lists')]))))
            )
        )
    );

    getSharePointListFields$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.getSharePointListFields),
        mergeMap((action) =>
            this.adminService.getSharePointListFields(action.id).pipe(
                map(data => AdminActions.getSharePointListFieldsSuccess({ fields: data })),
                catchError(error => of(AdminActions.getSharePointListFieldsFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load SharePoint list fields')]))))
            )
        )
    );

    saveInformationSource$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.saveInformationSource),
        mergeMap((action) =>
            this.adminService.saveInformationSource(action.source).pipe(
                mergeMap(data => [AdminActions.saveInformationSourceSuccess({ id: action.source.InformationSourceId, result: data }), AdminActions.loadCompositeModel()]),
                catchError(error => of(AdminActions.saveInformationSourceFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to save information source')]))))
            )
        )
    );

    deleteInformationSource$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.deleteInformationSource),
        mergeMap((action) =>
            this.adminService.deleteInformationSource(action.id).pipe(
                map(data => AdminActions.deleteInformationSourceSuccess({ id: action.id, result: data })),
                catchError(error => of(AdminActions.deleteInformationSourceFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to delete information source')]))))
            )
        )
    );

    testInformationSource$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.testInformationSource),
        switchMap((action) =>
            this.adminService.testInformationSource(action.id).pipe(
                mergeMap(data => {
                    const actions: Action[] = [AdminActions.testInformationSourceSuccess({ result: data })];
                    if (data.startsWith('ERROR')) {
                        actions.push(new ToastError(data));
                    } else {
                        actions.push(new ToastSuccess(data));
                    }
                    return actions;
                }),
                catchError(error => of(AdminActions.testInformationSourceFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to test information source')]))))
            )
        )
    );

    loadApiKeys$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.loadApiKeys),
        switchMap((action) =>
        this.adminService.loadApiKeys().pipe(
            map(data => AdminActions.loadApiKeysSuccess({ apiKeys: data })),
            catchError(error => of(AdminActions.loadApiKeysFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to load API keys')]))))
        )
    ));

    createApiKey$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.createApiKey),
        switchMap((action) =>
        this.adminService.createApiKey().pipe(
            map(data => AdminActions.createApiKeySuccess({ apiKey: data })),
            catchError(error => of(AdminActions.createApiKeyFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to create an API key')]))))
        )
    ));

    revokeApiKey$ = createEffect(() => this.actions$.pipe(
        ofType(AdminActions.revokeApiKey),
        switchMap((action) =>
        this.adminService.revokeApiKey(action.id).pipe(
            map(data => AdminActions.revokeApiKeySuccess({ id: action.id })),
            catchError(error => of(AdminActions.revokeApiKeyFailure()).pipe(mergeMap(x => [x, new ToastError('Failed to revoke an API key')]))))
        )
    ));
}
