import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CompositeModel, SecurityGroup, SecurityChartViewModel, SecurityGroupEntry, AvailableEntries, InformationSourceViewModel, SharePointListInfo, ApiKey } from '../admin.models';
import { AppearanceFormValue } from '../admin.reducers';

@Injectable()
export class AdminService {
    private API_PATH = '/api/admin';
    constructor(private http: HttpClient) {}

    loadAdminCompositeData(): Observable<CompositeModel> {
        return this.http.get<CompositeModel>(`${this.API_PATH}/composite`);
    }

    loadCompanyGroups(): Observable<AvailableEntries> {
        return this.http.get<AvailableEntries>(`/api/security/availableentries`);
    }

    loadSecurityCharts(): Observable<SecurityChartViewModel[]> {
        return this.http.get<SecurityChartViewModel[]>(`/api/security/charts`);
    }

    loadCacheSetting(): Observable<boolean> {
        return this.http.get<boolean>(`${this.API_PATH}/disablecache`);
    }

    saveAppearance(form: AppearanceFormValue): Observable<object> {
        return this.http.post(`${this.API_PATH}/settings`, {
            CustomColour: form.brandColor,
            CustomFooterText: form.footerText,
            CustomThemeName: form.theme
        });
    }

    removeLogo() {
        return this.http.post(`${this.API_PATH}/logo/remove`, {});
    }

    removeCss() {
        return this.http.delete(`${this.API_PATH}/customcss`);
    }

    postCreators(creators: string[]): Observable<string[]> {
        return this.http.post<string[]>(`${this.API_PATH}/creators`, creators);
    }

    getPersonName(id: string): Observable<string> {
        return this.http.get<string>(`/api/picker/personname/?id=${encodeURIComponent(id)}`);
    }

    addSecurityGroup(name: string): Observable<SecurityGroup> {
        return this.http.post<SecurityGroup>(`/api/security/securitygroups`, { primitive: name });
    }

    removeSecurityGroup(id: number): Observable<SecurityGroup[]> {
        return this.http.delete<SecurityGroup[]>(`/api/security/securitygroups/${id}`);
    }

    getSecurityCharts(): Observable<SecurityChartViewModel[]> {
        return this.http.get<SecurityChartViewModel[]>(`/api/security/charts`);
    }

    addSecurityGroupEntry(entry: SecurityGroupEntry): Observable<SecurityGroup> {
        return this.http.post<SecurityGroup>(`/api/security/securitygroupentry`, entry);
    }

    removeSecurityGroupEntry(id: number): Observable<SecurityGroup> {
        return this.http.delete<SecurityGroup>(`/api/security/securitygroupentry/${id}`);
    }

    saveSecurityChart(chart: SecurityChartViewModel): Observable<SecurityChartViewModel> {
        return this.http.post<SecurityChartViewModel>(`/api/security/chartsecuritygroups/${chart.id}`, chart);
    }

    saveExternalUsersSetting(chart: SecurityChartViewModel): Observable<SecurityChartViewModel> {
        return this.http.post<SecurityChartViewModel>(`/api/security/externalusers/${chart.id}`, {
            ...chart,
            viewGroupId: 0,
            adminGroupId: 0,
            denyGroupId: 0
        });
    }

    saveGlobalExternalUsersSetting(value: boolean): Observable<boolean> {
        return this.http.post<boolean>(`/api/security/externalusers/global`, { areExternalUsersAllowed: value });
    }

    saveAdminEmail(AdminEmail: string, CompanyName: string) {
        return this.http.post(`${this.API_PATH}/adminemail`, { AdminEmail, CompanyName });
    }

    saveCacheSetting(value: boolean): Observable<boolean> {
        return this.http.post<boolean>(`${this.API_PATH}/disablecache`, { DisableCache: value });
    }

    getInfoSource(sourceId: string): Observable<InformationSourceViewModel> {
        return this.http.get<InformationSourceViewModel>(`${this.API_PATH}/informationsource?sourceId=${sourceId}`);
    }

    getSharePointLists(): Observable<SharePointListInfo[]> {
        return this.http.get<SharePointListInfo[]>('/api/sharepoint/alllists');
    }

    getSharePointListFields(id: string): Observable<Array<{[paramName: string]: string}>> {
        return this.http.get<Array<{[paramName: string]: string}>>(`/api/sharepoint/fields/${id}/Host`);
    }

    saveInformationSource(source: InformationSourceViewModel): Observable<boolean> {
        return this.http.post<boolean>(`/api/admin/saveinformationsource`, source);
    }

    deleteInformationSource(sourceId: string): Observable<boolean> {
        return this.http.get<boolean>(`${this.API_PATH}/removeinformationsource?sourceId=${sourceId}`);
    }

    testInformationSource(sourceId: string): Observable<string> {
        return this.http.get<string>(`/api/info/test?sourceId=${sourceId}`);
    }

    loadApiKeys(): Observable<ApiKey[]> {
        return this.http.get<ApiKey[]>(`${this.API_PATH}/apikeys`);
    }

    createApiKey(): Observable<ApiKey> {
        return this.http.post<ApiKey>(`${this.API_PATH}/apikeys`, {});
    }

    revokeApiKey(id: number) {
        return this.http.delete(`${this.API_PATH}/apikeys/${id}`);
    }
}
