import { createSelector, createFeatureSelector, createReducer, on, Action } from '@ngrx/store';
import * as AdminActions from './admin.actions';
import * as fromRoot from '../reducers';
import { CompositeModel, AvailableEntries, SecurityChartViewModel, InformationSourceViewModel, SharePointListInfo, ApiKey } from './admin.models';
import { FormGroupState, createFormGroupState, onNgrxForms, wrapReducerWithFormStateUpdate } from 'ngrx-forms';

export interface AdminState {
    compositeModel: CompositeModel;
    availableEntries: AvailableEntries;
    isFetching: boolean;
    appearanceForm: FormGroupState<AppearanceFormValue>;
    selectedSecurityGroupId: number;
    charts: SecurityChartViewModel[];
    chartRequests: { [paramName: string]: any };
    chartExternalRequests: { [paramName: string]: any };
    disableCacheSetting: boolean;
    selectedSourceInfo: InformationSourceViewModel;
    deletedSourceId: string;
    savedSourceId: string;
    sharePointLists: SharePointListInfo[];
    sharePointListFields: Array<{ [paramName: string]: string }>;
    apiKeys: ApiKey[];
}

export interface AppearanceFormValue {
    theme: string;
    brandColor: string;
    footerText: string;
}

export interface State extends fromRoot.State {
    admin: AdminState;
}

const APPEARANCE_FORM_ID = 'appearance_form_id';

export const initialAppearanceFormState = createFormGroupState<AppearanceFormValue>(APPEARANCE_FORM_ID, {
    brandColor: '#000000',
    theme: 'Light',
    footerText: ''
});

export const initialState: AdminState = {
    compositeModel: null,
    isFetching: false,
    appearanceForm: initialAppearanceFormState,
    selectedSecurityGroupId: 0,
    availableEntries: null,
    charts: null,
    chartRequests: {},
    chartExternalRequests: {},
    disableCacheSetting: null,
    selectedSourceInfo: null,
    deletedSourceId: null,
    savedSourceId: null,
    sharePointLists: [],
    sharePointListFields: [],
    apiKeys: []
};

const adminReducer = createReducer(
    initialState,
    onNgrxForms(),
    on(AdminActions.setAppearanceFormFromCompositeModel, (state, { model }) => {
        return {
            ...state,
            appearanceForm: createFormGroupState<AppearanceFormValue>(APPEARANCE_FORM_ID, {
                ...initialAppearanceFormState,
                brandColor: model.Settings.CustomColour,
                theme: model.Settings.CustomThemeName,
                footerText: model.Settings.CustomFooterText
            })
        };
    }),
    on(AdminActions.saveAppearanceSuccess, state => {
        window.location.reload();
        return state;
    }),
    on(AdminActions.loadCompositeModel, state => ({ ...state, isFetching: true })),
    on(AdminActions.loadCompositeModelFailure, state => ({ ...state, isFetching: false })),
    on(AdminActions.loadCompositeModelSuccess, (state, { model }) => ({
        ...state,
        compositeModel: model,
        isFetching: false
    })),
    on(AdminActions.saveCreatorsSuccess, (state, { creators }) => ({
        ...state,
        compositeModel: { ...state.compositeModel, Creators: creators }
    })),
    on(AdminActions.createSecurityGroupSuccess, (state, { group }) => ({
        ...state,
        selectedSecurityGroupId: group.Id,
        compositeModel: { ...state.compositeModel, SecurityGroups: [...state.compositeModel.SecurityGroups, group] }
    })),
    on(AdminActions.removeSecurityGroupSuccess, (state, { groups }) => ({
        ...state,
        compositeModel: { ...state.compositeModel, SecurityGroups: groups }
    })),
    on(AdminActions.loadCompanyGroupsSuccess, (state, { entries }) => ({ ...state, availableEntries: entries })),
    on(AdminActions.addSecurityGroupEntrySuccess, AdminActions.removeSecurityGroupEntrySuccess, (state, { group }) => {
        return {
            ...state,
            compositeModel: {
                ...state.compositeModel,
                SecurityGroups: state.compositeModel.SecurityGroups.map(x => {
                    if (x.Id === group.Id) {
                        return group;
                    }
                    return x;
                })
            }
        };
    }),
    on(AdminActions.saveGlobalExternalSettingSuccess, (state, { value }) => ({
        ...state,
        compositeModel: { ...state.compositeModel, GlobalExternalUsersSetting: value }
    })),
    on(AdminActions.saveExternalUsersSettingSuccess, (state, { chart }) => {
        const { [chart.id]: value, ...requests } = state.chartExternalRequests;
        return {
            ...state,
            charts: state.charts.map(x => {
                if (x.id === chart.id) {
                    //this particular API does not return the full chart view model, only id and areExternalUsersAllowed fields
                    return { ...x, areExternalUsersAllowed: chart.areExternalUsersAllowed };
                }
                return x;
            }),
            chartExternalRequests: requests
        };
    }),
    on(AdminActions.saveExternalUsersSetting, (state, { chart }) => {
        return { ...state, chartExternalRequests: { ...state.chartExternalRequests, [chart.id]: {} } };
    }),
    on(AdminActions.saveChart, (state, { chart }) => {
        return { ...state, chartRequests: { ...state.chartExternalRequests, [chart.id]: {} } };
    }),
    on(AdminActions.saveChartSuccess, AdminActions.saveChartFailure, (state, { chart }) => {
        const { [chart.id]: value, ...requests } = state.chartRequests;
        return {
            ...state,
            chartRequests: requests,
            charts: state.charts.map(x => {
                if (x.id === chart.id) {
                    return chart;
                }
                return x;
            })
        };
    }),
    on(AdminActions.loadChartsSuccess, (state, { charts }) => ({
        ...state,
        charts: charts.map(x => {
            const chart = { ...x };
            return chart;
        })
    })),
    on(AdminActions.saveAdminEmailSuccess, (state, { AdminEmail, CompanyName }) => ({
        ...state,
        compositeModel: {
            ...state.compositeModel,
            AdminEmailAndCompanyName: {
                AdminEmail,
                CompanyName
            }
        }
    })),
    on(AdminActions.loadCacheSettingSuccess, (state, { value }) => ({ ...state, disableCacheSetting: value })),
    on(AdminActions.saveCacheSettingSuccess, (state, { value }) => ({ ...state, disableCacheSetting: value })),
    on(AdminActions.loadInformationSourceSuccess, (state, { source }) => ({ ...state, selectedSourceInfo: source })),
    on(AdminActions.deleteInformationSourceSuccess, (state, { id, result }) => ({
        ...state,
        deletedSourceId: result ? id : null
    })),
    on(AdminActions.saveInformationSourceSuccess, (state, { id, result }) => {
        if (result) {
            return { ...state, savedSourceId: id };
        }
        return state;
    }),
    on(AdminActions.clearSavedSourceId, (state) => ({ ...state, savedSourceId: null })),
    on(AdminActions.getSharePointListsSuccess, (state, { lists }) => ({ ...state, sharePointLists: lists })),
    on(AdminActions.getSharePointListFieldsSuccess, (state, { fields }) => ({ ...state, sharePointListFields: fields })),
    on(AdminActions.loadApiKeysSuccess, (state, { apiKeys }) => ({ ...state, apiKeys })),
    on(AdminActions.createApiKeySuccess, (state, { apiKey }) => ({ ...state, apiKeys: [...state.apiKeys, apiKey] })),
    on(AdminActions.revokeApiKeySuccess, (state, { id }) => ({ ...state, apiKeys: state.apiKeys.filter(x => x.Id !== id) }))
);

export function reducer(state: AdminState | undefined, action: Action) {
    return adminReducer(state, action);
}

export const selectAdmin = (state: State) => state.admin;

export const selectCompositeModel = createSelector(selectAdmin, state => state.compositeModel);

export const selectAppearanceFormState = createSelector(selectAdmin, state => state.appearanceForm);

export const selectSelectedSecurityGroupId = createSelector(selectAdmin, state => state.selectedSecurityGroupId);

export const selectAvailableEntries = createSelector(selectAdmin, s => s.availableEntries);

export const selectCharts = createSelector(selectAdmin, s => s.charts);

export const selectChartRequests = createSelector(selectAdmin, s => s.chartRequests);

export const selectChartExternalRequests = createSelector(selectAdmin, s => s.chartExternalRequests);

export const selectDisableCacheSetting = createSelector(selectAdmin, s => s.disableCacheSetting);

export const selectSelectedSourceInfo = createSelector(selectAdmin, s => s.selectedSourceInfo);

export const selectApiKeys = createSelector(selectAdmin, s => s.apiKeys);

export const selectDeletedSourceId = createSelector(
    selectAdmin,
    (state: AdminState) => state.deletedSourceId
);

export const selecteSavedSourceId = createSelector(
    selectAdmin,
    s => s.savedSourceId
);

export const selectSharePointLists = createSelector(
    selectAdmin,
    (state: AdminState) => state.sharePointLists
);

export const selectSharePointListFields = createSelector(
    selectAdmin,
    (state: AdminState) => state.sharePointListFields
);
