import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { FormsModule } from '@angular/forms';
import { FileUploadModule } from 'ng2-file-upload';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import * as fromAdmin from './admin.reducers';
import { ColorPickerModule } from 'ngx-color-picker';
import { SharedModule } from '../shared/shared.module';
import { AppearanceComponent } from './components/appearance.component';
import { SecurityComponent } from './components/security.component';
import { AdminEffects } from './admin.effects';
import { AdminService } from './services/admin.service';
import { AdminRoutingModule } from './admin-routing.module';
import { NgrxFormsModule } from 'ngrx-forms';
import { AccountComponent } from './components/account.component';
import { InformationSourcesComponent } from './components/information-sources.component';
import { ApiComponent } from './components/api.component';
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { ClipboardModule } from 'ngx-clipboard';
import { ApiGuideComponent } from './components/api-guide.component';

@NgModule({
    imports: [
        CommonModule,
        AdminRoutingModule,
        StoreModule.forFeature('admin', fromAdmin.reducer),
        EffectsModule.forFeature([AdminEffects]),
        TooltipModule.forRoot(),
        FileUploadModule,
        FormsModule,
        NgrxFormsModule,
        SharedModule,
        TypeaheadModule,
        ColorPickerModule,
        ClipboardModule
    ],
    declarations: [
        AppearanceComponent,
        SecurityComponent,
        AccountComponent,
        InformationSourcesComponent,
        ApiComponent,
        ApiGuideComponent
    ],
    providers: [
        AdminService
    ]
})
export class AdminModule {}
